@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: calc(50vh - 150px);
}

#metronomeWrapper {
    border-radius: 30px 30px 10px 10px;
    box-shadow: 0 0 20px 1px rgba(0,0,0,0.5);
}
#metronome {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    width: 500px;
    height: 300px;
    background: #36447f;
    border-radius: 30px 30px 10px 10px;
    box-shadow: inset 0 0 10px #000;
    font-family: "roboto";
}
#blackBottomPanel {
    margin: 0 auto;
    background: rgb(0,0,0);
    background: linear-gradient(180deg, rgba(0,0,0,1) 50%, rgba(70,70,70,1) 100%);
    border-radius: 2px;
    height: 50px;
    width: calc(100% - 10px);
    z-index: 10;
}
#buttonsWrapper {
    margin: 0 40px;
    background: #36447f;
    height: 45px;
    width: calc(100% - 80px);
    border-radius: 20px;
    z-index: 20;
    position: relative;
    top: 20px;
    display: flex;
    padding: 5px;
    justify-content: space-between;
    flex-direction: row;
    align-items: flex-end;
}
.buttonWrapper {
}
.buttonLabel {
    color: white;
    text-align: center;
    font-size: 10px;
    margin-bottom: 5px;
}
.button {
    background: black;
    cursor: pointer;
    box-shadow: 0 0 5px 2px rgba(0,0,0,0.4);
}
#power {
    width: 30px;
    height: 30px;
    border-radius: 15px;
}
.oval {
    width: 40px;
    height: 30px;
    border-radius: 15px;
}
.circle {
    width: 40px;
    height: 40px;
    border-radius: 20px;
}
#screenWrapper {
    background: #5b5e57;
    box-shadow: inset 0 0 10px #000000;
    width: 250px;
    height: 130px;
    margin: 10px 0;
    padding: 10px;
    position: relative;
    left: 40px;
    font-family: "digital numbers";
    font-size: 40px;
}
#clause {
  position: absolute;
  bottom: 10px;
  font-size: 10px;
}
#clause a {
    text-decoration: none;
    font-weight: 700;
    color: #36447f;
}
@font-face {
    font-family: "digital numbers";
    src: url(/metronome/static/media/DigitalNumbers-Regular.a1fd4994.woff) format("woff");
}
@font-face {
    font-family: "roboto";
    src: url(/metronome/static/media/Roboto-Regular.a8d6ac03.ttf) format("ttf");
}
@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: portrait) {
    html {
      -webkit-transform: rotate(-90deg);
              transform: rotate(-90deg);
      -webkit-transform-origin: left top;
              transform-origin: left top;
      width: 100vh;
      overflow-x: hidden;
      position: absolute;
      top: 100%;
      left: 0;
    }
    .App {
        padding-top: calc(50vw - 150px);
        height: 100vw;
    }
    #metronomeWrapper {
        margin-bottom: 20px;
    }
    #clause {
        z-index: 50;
        bottom: 0;
    }
  }
