#buttonsWrapper {
    margin: 0 40px;
    background: #36447f;
    height: 45px;
    width: calc(100% - 80px);
    border-radius: 20px;
    z-index: 20;
    position: relative;
    top: 20px;
    display: flex;
    padding: 5px;
    justify-content: space-between;
    flex-direction: row;
    align-items: flex-end;
}